/*
    Multi Column Sorting
    Helper element for:
    - Manual Column Sorting
*/

@mixin output {
    .handsontable {
        /* Column's number position */
        span.colHeader.columnSorting {
            &:after {
                position: relative;
                order: 2;
                margin-left: calc( var(--hot-base-grid) * -1 );
                padding-inline-start: 1px;
                padding-inline-end: 1px;
                top: var(--hot-base-grid);
                font-size: 7px;
            }

            /* We support up to 7 numeric indicators, describing order of column in sorted columns queue */
            &[class^="sort-"]::after,
            &[class*=" sort-"]::after {
                content: "+";
            }

            /* Numeric indicators */
            @for $i from 1 through 7 {
                &.sort-#{$i}::after {
                    content: '#{$i}';
                }
            }
        }
    }

    /* Drop-down menu */
    .htGhostTable th div button.changeType + span.colHeader.columnSorting:not(.indicatorDisabled) {
        padding-right: var(--hot-base-grid);
    }
}