/*
    Handsontable HiddenColumns
*/

@mixin output {
    .handsontable {
        th.beforeHiddenColumn,
        th.afterHiddenColumn {
            position: relative;

            &:after, 
            &:before {
                color: var(--hot-hidden-dimension-indicator-color);
                position: absolute;
                top: 50%;
                font-size: 5pt;
                transform: translateY(-50%);
            }
        }

        th.beforeHiddenColumn::after {
            right: var(--hot-hidden-dimension-indicator-margin-column);
            content: '\25C0'; /* left arrow */
        }

        th.afterHiddenColumn::before {
            left: var(--hot-hidden-dimension-indicator-margin-column);
            content: '\25B6'; /* right arrow */
        }

        &.ht__selection--columns {
            th.beforeHiddenColumn.ht__highlight:after,
            th.afterHiddenColumn.ht__highlight:before {
                color: var(--hot-hidden-dimension-indicator-color-highlighted);
            }
        }
    }

    [dir=rtl].handsontable {
        th.beforeHiddenColumn::after {
            right: initial;
            left: var(--hot-hidden-dimension-indicator-margin-column);
            content: '\25B6'; /* right arrow */
        }

        th.afterHiddenColumn::before {
            right: var(--hot-hidden-dimension-indicator-margin-column);
            left: initial;
            content: '\25C0'; /* left arrow */
        }
    }
}